<template>
  <main-page-base
    :requiredModules="requiredModules"
    :sortTypes="sortTypes"
    :getMultidexRouteParamsTo="getMultidexRouteParamsTo"
    :inputServer="server"
    :viewId="viewId"
    :pageDb="pageDb"
    :inputFilters="filters"
    :filterTypes="filterTypes"
    :getCompareName="(id, entry) => (entry && entry.name) || id"
    compareType="bb"
  >
    <v-layout row wrap slot="results" slot-scope="{ keys, getMultidexPathTo }">
      <v-flex
        v-for="key in keys"
        :key="key"
        xs12 sm6 md4 xl3>
        <entry-card :to="getMultidexPathTo(key)" :entry="pageDb[key]" v-if="pageDb.hasOwnProperty(key)"/>
      </v-flex>
    </v-layout>
    <template slot="dialog-toolbar-title" slot-scope="{ viewId, hasViewId, entry }">
        <span v-if="hasViewId">
          {{ entry.name }}
        </span>
        <span v-else>
          Bursts Entry: {{ viewId }}
        </span>
    </template>
    <template slot="entry-dialog-content" slot-scope="{ viewId, logger }">
      <dialog-content
        :entryId="viewId"
        :logger="logger"
        :pageDb="pageDb"
        :asyncGetById="getById"/>
    </template>
    <template slot="compare-input-selection" slot-scope="{ selectionId, selectionName }">
      <v-chip small>
        <span v-text="`${selectionName} (${selectionId})`"/>
      </v-chip>
    </template>
  </main-page-base>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import MultidexPageMixin from '@/components/Multidex/MultidexPageMixin';
import EntryCard from '@/components/Multidex/Bursts/EntryCard';
import DialogContent from '@/components/Multidex/Bursts/DialogContent';

export default {
  mixins: [MultidexPageMixin],
  components: {
    EntryCard,
    DialogContent,
  },
  computed: {
    ...mapState('bursts', ['pageDb']),
    ...mapGetters('bursts', ['getMultidexRouteParamsTo', 'sortTypes', 'requiredModules', 'filterTypes']),
  },
  methods: {
    ...mapActions('bursts', ['getById']),
  },
};
</script>
